import React, { useEffect, useState } from 'react';
import './App.css';
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import LeaderEditor from './views/LeaderEditor';

const Root = () => {
  const { i18n } = useTranslation();
  const [ready, setReady] = useState(false);
  useEffect(() => {
    i18n.changeLanguage(window.localStorage.getItem('lang') || 'en');
    setReady(true); // To prevent resources being fetched before language is set
  }, []);
  return ready && (
    <>
      <Router>
        <Switch>
          <Route exact path="/" component={LeaderEditor} />
          <Redirect to="/" />
        </Switch>
      </Router>
    </>
  );
};

export default Root;
